body {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.card_container {
  margin: 4em auto;
  width: 65%;

  @include resposibo(tablet) {
    width: 60%;
  }

  @include resposibo(phone) {
    width: 85%;
  }

  &__content {
    width: 100%;
    height: 450px;
    box-shadow: 0 0 20px rgb(160, 156, 156);
    border-radius: 8px;

    &__img {
      position: relative;
      float: left;
      height: 100%;
      width: 60%;
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;

      @include resposibo(tablet) {
        display: none;
      }

      @include resposibo(phone) {
        display: none;
      }

      img {
        width: 100%;
        height: inherit;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
      }
    }
    &__form {
      position: relative;
      float: right;
      width: 40%;
      height: 100%;
      background-color: rgb(255, 255, 255);
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;

      .form_titulo {
        text-align: center;
      }
      @include resposibo(tablet) {
        width: 100%;
        border-radius: 8px;
      }

      @include resposibo(phone) {
        width: 100%;
        border-radius: 8px;
      }
    }
    .form {
      &__content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 3em;

        .boton_form {
          background-color: rgb(46, 89, 217);
          color: rgb(255, 255, 255);
          width: 90%;
          margin: 10px;
          padding: 10px;
          border-radius: 8px;
          font-size: 16px;
          border: none;

          &:hover {
            background-color: rgb(0, 54, 218);
            color: rgb(241, 237, 237);
            cursor: pointer;
          }
        }

        span {
          margin: 10px 0;
        }

        &__enlace {
          text-decoration: none;
          color: rgb(62, 62, 235);

          &:hover {
            text-decoration: underline;
          }
        }

        input {
          padding-left: 15px;
          margin: 10px 10px;
          height: 2.3em;
          width: 100%;
          border-radius: 8px;
          border: 1px solid rgb(160, 156, 156);
          font-size: 16px;

          &:focus {
            border-radius: 8px;
            box-shadow: 0 0 0 3px rgb(138, 183, 226);
            outline: none;
          }
        }
      }
    }
  }
}
